"use client";

import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { format } from "date-fns";
import { Calendar, Clock, User, Users, MapPin, Monitor } from "lucide-react";

// Define Session interface (adjust based on actual data structure passed)
interface SessionData {
  id: string;
  title: string;
  start?: Date; // Allow start to be optional
  end?: Date;   // Allow end to be optional
  student?: { // Optional for student view
    name: string;
  };
  tutor?: { // Optional for tutor view
    name: string;
  };
  status: "upcoming" | "completed";
  type: "online" | "in-person";
  resource?: any; // Catch-all for additional data if needed
}

interface SessionDetailModalProps {
  isOpen: boolean;
  onClose: () => void;
  session: SessionData | null;
  role: "TUTOR" | "STUDENT"; // To display relevant info (Tutor/Student name)
}

export function SessionDetailModal({
  isOpen,
  onClose,
  session,
  role,
}: SessionDetailModalProps) {
  if (!session) return null;

  // Check for start/end date existence before formatting
  const startDateFormatted = session.start ? format(session.start, "MMMM d, yyyy") : "N/A";
  const startTimeFormatted = session.start ? format(session.start, "h:mm a") : "N/A";
  const endTimeFormatted = session.end ? format(session.end, "h:mm a") : "N/A";

  const isUpcoming = session.status === "upcoming" || session.resource?.status === "upcoming";
  const personLabel = role === "TUTOR" ? "Student" : "Tutor";
  const personName = role === "TUTOR" ? session.student?.name : session.tutor?.name;

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[480px]">
        <DialogHeader>
          <DialogTitle className="text-xl">{session.title}</DialogTitle>
          <DialogDescription>
            Details for your session.
          </DialogDescription>
        </DialogHeader>
        <div className="grid gap-4 py-4">
          {/* Status */}
          <div className="flex items-center justify-between">
            <span className="text-sm text-muted-foreground">Status</span>
            <Badge variant={isUpcoming ? "default" : "secondary"} className={isUpcoming ? 'bg-primary/10 text-primary border-primary/20' : ''}>
              {isUpcoming ? "Upcoming" : "Completed"}
            </Badge>
          </div>

          {/* Date & Time - Use formatted variables */}
          <div className="flex items-center justify-between">
            <span className="text-sm text-muted-foreground inline-flex items-center gap-2"><Calendar size={16} /> Date</span>
            <span className="text-sm font-medium">{startDateFormatted}</span>
          </div>
          <div className="flex items-center justify-between">
            <span className="text-sm text-muted-foreground inline-flex items-center gap-2"><Clock size={16} /> Time</span>
            <span className="text-sm font-medium">
              {startTimeFormatted} - {endTimeFormatted}
            </span>
          </div>

           {/* Student/Tutor */}
           {personName && (
             <div className="flex items-center justify-between">
               <span className="text-sm text-muted-foreground inline-flex items-center gap-2">
                 {role === 'TUTOR' ? <User size={16} /> : <Users size={16}/>} {personLabel}
               </span>
               <span className="text-sm font-medium">{personName}</span>
             </div>
           )}

          {/* Type (Online/In-person) */}
          <div className="flex items-center justify-between">
            <span className="text-sm text-muted-foreground inline-flex items-center gap-2">
                {session.type === 'online' ? <Monitor size={16} /> : <MapPin size={16}/>} Type
            </span>
            <span className="text-sm font-medium capitalize">{session.type}</span>
          </div>

          {/* Add more details here if needed, e.g., location, subject, notes */}

        </div>
        <DialogFooter>
          {/* Add relevant actions if needed, e.g., "Join Session", "Cancel Session" */}
          <Button variant="outline" onClick={onClose}>
            Close
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
} 